 import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import {NgbModule} from '@ng-bootstrap/ng-bootstrap';

import { AppComponent } from './app.component';
import { PartiesComponent } from './components/parties/parties.component';
import {AppRoutingModule} from "./app-routing.module";
import {ServerService} from "./services/server-service";
import { HttpModule} from "@angular/http";
import {AgGridModule} from 'ag-grid-angular/main';
import { PartyComponent } from './components/parties/party/party.component';
import { PartyNameComponent } from './components/parties/party/party-name/party-name.component';
import { PartyPhysicalAddressComponent } from './components/parties/party/party-physical-address/party-physical-address.component';
import { PartyActiveJobCountComponent } from './components/parties/party/party-active-job-count/party-active-job-count.component';

@NgModule({
  declarations: [
    AppComponent,
    PartiesComponent,
    PartyComponent,
    PartyNameComponent,
    PartyPhysicalAddressComponent,
    PartyActiveJobCountComponent,
  ],
  imports: [
    BrowserModule,
    HttpModule,
    NgbModule.forRoot(),
    AppRoutingModule,
    AgGridModule.withComponents([PartyNameComponent, PartyPhysicalAddressComponent, PartyActiveJobCountComponent]),
  ],
  providers: [ServerService],
  bootstrap: [AppComponent]
})
export class AppModule { }



