import {AfterViewInit, ChangeDetectorRef, Component, DoCheck, Input, OnInit} from '@angular/core';
import {ServerService} from "../../services/server-service";
import {Response} from "@angular/http";
import customerColumns from "../../models/customer";
import {GridOptions} from "ag-grid";
import {Subject} from "rxjs/Subject";
import {NavBarService} from "../../services/nav-bar.service";
import {PartyNameComponent} from "./party/party-name/party-name.component";
import {PartyPhysicalAddressComponent} from "./party/party-physical-address/party-physical-address.component";
import {PartyActiveJobCountComponent} from "./party/party-active-job-count/party-active-job-count.component";
import {ActivatedRoute, Router} from "@angular/router";

@Component({
  selector: 'app-customers',
  templateUrl: './parties.component.html',
  styleUrls: ['./parties.component.scss']
})
export class PartiesComponent implements OnInit, AfterViewInit {
  customerColumns = customerColumns;
  gridOptions: GridOptions;
  rowData: any[];

  constructor(private serverService: ServerService,
              private navBarService: NavBarService,
              private changeDetectorRef: ChangeDetectorRef,
              private router:Router,
              private activatedRoute:ActivatedRoute) {
  }

  ngOnInit() {
    this.serverService.getAllParties()
      .subscribe((response: any) => {
        this.rowData = response._embedded.parties;
        console.log(this.rowData);
      }, (error) => {
        console.log(error);
      });
    this.gridOptions = {
      context: {
        componentParent: this
      },
      rowData: this.rowData,
      columnDefs: this.columnDefs
    };
    this.navBarService.getNavBarSubject().subscribe((value) => {
      // the timings of the nav bar css transition should be considered
      setTimeout(() => {
        this.gridOptions.api.sizeColumnsToFit();
      }, 1500);
    })
  }

  newCustomer() {

  }

  columnDefs = [
    {
      headerName: "Party Name",
      field: 'name',
      cellRendererFramework: PartyNameComponent,
    },
    {
      headerName: "Address",
      field: "physical_address",
      cellRendererFramework: PartyPhysicalAddressComponent,
      width: 550,
    },
    {
      headerName: "Active Jobs Count",
      field: "active_job_count",
      cellRendererFramework: PartyActiveJobCountComponent,
    }
  ];

  onModelUpdated() {
    console.log("onModeUpdated");
  }

  onCellClicked($event) {
    console.log($event.data._links.self.href);
    this.router.navigate(['party', {url:$event.data._links.self.href}],
        { queryParams: {},
          skipLocationChange:true})
        .then((value)=>{
        console.log(value);
    }).catch((error)=>{console.log(error)});
  }

  onCellDoubleClicked($event) {
    console.log($event.data._links.self);
  }

  ngAfterViewInit() {
    this.gridOptions.api.sizeColumnsToFit();
  }
}



