import { Injectable } from '@angular/core';
import {Http, RequestOptionsArgs, Response} from '@angular/http';
import 'rxjs/Rx';

import {Party} from "../models/customer";
import {Observable} from "rxjs/Observable";


@Injectable()
export class ServerService {

  constructor(private http: Http) {

  }
  optionsArgument:RequestOptionsArgs = "page=1";

  saveCustomer(customer: Party) {
    console.log(customer);
    return this.http.post('http://localhost:8080/setParty', customer);
  }

  getAllParties() {
     return this.http.get('http://localhost:8080/api/parties')
       .map(
         (response: Response)=>{
           const data = response.json();
           console.log(data);
           return data;
         }).catch(
         (error: Response) => {
           return Observable.throw('Something went wrong');
         }
       );
  }

  getPartyByUrl(url:string) {
    console.log(url);
    return this.http.get(url)
      .map(
        (response: Response)=>{
          const data = response.json();
          console.log(data);
          return data;
        }).catch(
        (error: Response) => {
          return Observable.throw('Something went wrong');
        }
      );
  }


}



